//
// Define scale to 100% control
//
var Set100ProcControl = /*@__PURE__*/(function (Control) {
  function Set100ProcControl(opt_options) {
    var options = opt_options || {};

    var button = document.createElement('button');
    button.innerHTML = '1:1';

    var element = document.createElement('div');
    element.className = 'set-to-100proc ol-unselectable ol-control';
    element.appendChild(button);

    Control.call(this, {
      element: element,
      target: options.target,
    });

    button.addEventListener('click', this.handleSetTo100proc.bind(this), false);
  }

  if ( Control ) Set100ProcControl.__proto__ = Control;
  Set100ProcControl.prototype = Object.create( Control && Control.prototype );
  Set100ProcControl.prototype.constructor = Set100ProcControl;

  Set100ProcControl.prototype.handleSetTo100proc = function handleSetTo100proc () {
    this.getMap().getView().setResolution(1);
  };

  return Set100ProcControl;
}(Control));

// ----------------------------------------------------
// Labels inside polygons and polygon styles
// ----------------------------------------------------
// Create text and define its style
var createTextStyle = function (feature, resolution) {
  return new Text({
    font: '12px Calibri,sans-serif',
    fill: new Fill({color: '#fff'}),
    text: feature.get('name'),
  });
};

// polygonStyleFunction
function polygonStyleFunction(feature, resolution) {
  return new Style({
    stroke: new Stroke({
      color: 'red',
      width: 1,
    }),
    fill: new Fill({
      color: 'rgba(255, 0, 0, 0.1)',
    }),
    text: createTextStyle(feature, resolution),
  });
}

