// function to update the map layer upon selection of a ROI in the upper map
function updateLayer( picId ){
  let extent = pics[picId].extent;
  let imageSize = pics[picId].imageSize;
  let metersPerUnit = pics[picId].metersPerUnit;
  
  // create a new layer
  let newLayer = new ImageLayer({
        source: new Static({
          attributions: ' <a href="https://www.helsinki.fi/en/infrastructures/bioimaging/embi">EMBI</a>',
          url: pics[picId].url,
          imageSize: imageSize,
          projection: new Projection({
			code: 'pixel',
			units: 'pixels',
			extent: extent,
		  }),
          imageExtent: extent,
        }),
  });
  
  if (newLayer !== currentSecondLayer) {
    // have to generate a new view to provide new pixel size
    let view = new View({
      projection: new Projection({
        code: 'pixel',
        units: 'pixels',
        extent: pics[picId].extent,
        metersPerUnit: pics[picId].metersPerUnit,
      }),
      center: getCenter(pics[picId].extent),
      zoom: 1,
      maxZoom: 8,
    });

   map2.addLayer(newLayer);
   map2.removeLayer(currentSecondLayer);
   map2.setView(view);
   map2.getView().setZoom(1);
   map2.getView().fit(extent);
   currentSecondLayer = newLayer;
  }
}

// Popup showing the position the user clicked
var popup = new Overlay({
  element: document.getElementById('popup'),
});
map.addOverlay(popup);

// create feature source
var featuresSource = new GeoJSON().readFeatures(geojsonObject);

// create vector source
var vectorSource = new VectorSource({
  features: featuresSource,
});

// create vector layer and add it to map
var vectorLayer = new VectorLayer({
  source: vectorSource,
  //style: styles,
  style: polygonStyleFunction,
});
map.addLayer(vectorLayer);

// add Select action to map to highlight select feature
map.addInteraction(new Select());

// function to detect id of the clicked feature
var displayFeatureInfo = function (pixel) {
  vectorLayer.getFeatures(pixel).then(function (features) {
    var feature = features.length ? features[0] : undefined;
    
	var info = document.getElementById('info');
    if (features.length) {
      currentSecondLayer = updateLayer(feature.getId(), currentSecondLayer);
	  //console.log(feature.get('info'));
	  info.innerHTML = feature.get('info');
	  //info.innerHTML = feature.getId() + ': ' + feature.get('name');
    } else {
      //info.innerHTML = '&nbsp;';
    }
  });
};

// bind slider inputs to functions
Modules.setupBindInputs('#layer', map.getLayerGroup());

$('#layertree li > span')
  .click(function () {
    $(this).siblings('fieldset').toggle();
  })
  .siblings('fieldset')
  //.hide();

map.on('click', function (evt) {
  displayFeatureInfo(evt.pixel);
});



